body {
    background: url(https://patchwiki.biligame.com/images/ys/8/8e/fs0zegmjxowe043i2r430w2ss0vi9fg.jpg) no-repeat fixed 50% 0;
    text-align: center;
}
form {
    text-align: left;
}
.top {
    display: block;
    margin-top: 40px;

}
h1 {
    text-align: center;
}
.clearfix:after {
    content: "";
    display: block;
    height: 0px;
    clear: both;
    visibility: hidden;
}
.clearfix {
    zoom: 1;
}
.case {
    border: 2px slategray solid;
    margin-left: 20px;
    margin-right: 20px;
    position: relative;
    display: inline-block;
    background: white;
    width: auto;
    height: auto;

}
.box-left {
    border: black 1px solid;
    width: 30%;
    height: 830px;
    display: inline-block;
    background: #FAF9DE;
}
.box-right {
    border: black 1px solid;
    width: 50%;
}
.hero-img {
    width: 106px;
    height: 106px;
    border: slategray 1px solid;
    background: black;
    display: inline-block;
    position: static;
    margin-top: 5px;
    margin-left: 5px;
    float: left;

}
#hero-name-sel {
    display: block;
    width: 80px;
    position: static;

}
#hero-lv-sel {
    display: block;
    width: 80px;
    position: static;
    /*top: 20px;*/

}
#hero-evolve-sel {
    display: block;
    width: 80px;
    position: static;
    /*top: 40px;*/
}
.box-left-sel {
    width: 80px;
    height: 106px;
    display: inline-block;
    position: static;
    margin-left: 5px;
}
#form01 {
    display: block;
    /*float: left;*/
    position: absolute;
    width: auto;
    height: auto;
}
.hero-arrti {
    display: block;
    float: left;
    position: absolute;
    left: 10px;
    height: auto;
    top: 130px;
    text-align: left;
}
.form-01-box {
    width: 196px;
    height: 120px;
    display: block;
    float: left;
}
#tips-box {
    display: inline-block;
    float: right;
    width: 200px;
    margin-left: 20px;
    vertical-align: top;
}

#allupdate {
    width: 60px;
