.App {
  text-align: center;

}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
#block {
    width: 600px;
    height: 410px;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#xianglin {
    position: absolute;
    width: 35%;
    left: 0%;
    top: -10%;
}
#diona {
    position: absolute;
    width: 30%;
    left: 70%;
    top: 8%;
}
#arrow {
    position: absolute;
    display: block;
    top: 120%;
    left: 31%;
    z-index: 10;

}
