.artifact_box {
    width: 65%;
    height: 830px;
    margin-left: 15px;
    border: 1px gray solid;
    background: #FAF9DE;
    display: inline-block;
    vertical-align: top;
}
#black_box {
    height: 200px;
    width: auto;
    margin: 3px;
    border: 1px black solid;
}
#white_box {
    height: 620px;
    width: auto;
    margin: 3px;
    border: 1px black solid;
    overflow: scroll;
}

#artifact {
    width: 19.5%;
    height: 170px;
    display: inline-block;
    border: 1px slategray solid;
    background: greenyellow;
    vertical-align: top;
    margin-top: 2px;
}
#artifactbox {
    width: 19.5%;
    height: auto;
    display: inline-block;
    border: 1px slategray solid;
    background: yellow;
    vertical-align: top;
    margin-top: 2px;
}
#artifactpull {
    background: orange;
}
#showartifactpull {
    text-align: left;
    margin-bottom: 3px;
    border: 1px black solid;
}
li {
    list-style: none;
    text-align: left;
}
ul {
    padding-left: 0px;
    margin-top: 3px;
}
#allupdate {
    width: 60px;
    height: 65px;
